/**
 * article模块接口列表
 */

import base from './base'; // 导入接口域名列表
import axios from './../http'; // 导入http中创建的axios实例
import qs from 'qs'; // 根据需求是否导入qs模块

let url = base.serverHost;
const article = {
  // 新闻列表
  articleList () {
    return axios.get(`${url}/topics`);
  },
  // 新闻详情,演示
  articleDetail (id, params) {
    return axios.get(`${url}/topic/${id}`, {
      params: params
    });
  },
  // post提交
  login (params) {
    return axios.post(`${url}/accesstoken`, qs.stringify(params));
  },

  // post提交
  schManger (path,params) {
    return axios.post(`${url}/sch/${path}`, params);
  },
  // post提交
  agencyManger (path,params) {
    return axios.post(`${url}/age/${path}`, params);
  },
  // post提交
  emManger (path,params) {
    return axios.post(`${url}/em/${path}`, params);
  },
  // post提交
  agencyPrjManger (path,params) {
    return axios.post(`${url}/agencyPrj/${path}`, params);
  },
  // post提交
  schPrjManger (path,params) {
    return axios.post(`${url}/schPrj/${path}`, params);
  },
// post提交
  screenShow (path,params) {
    return axios.post(`${url}/show/${path}`, params);
  },

  // 为了获取静态资源的路径拼接
  upImage (path,params) {
    return axios.post(`${url}/${path}`, params);
  },


  // 为了获取静态资源的路径拼接
  getUpPathForTouXiang (path) {
    return `${url}/em/${path}`;
  },

  // 为了获取静态资源的路径拼接
  getImageForTouXiang (path) {
    return `${url}/images/${path}`;
  },




  //封装的测试使用类
  testSome (params) {
    alert(params);
    alert(`${base.local}`);
  },

  // 其他接口…………
}

export default article;

//1.通过直接引入我们封装好的axios实例，然后定义接口、调用axios实例并返回，可以更灵活的使用axios，比如你可以对post请求时提交的数据进行一个qs序列化的处理等。
// 2.请求的配置更灵活，你可以针对某个需求进行一个不同的配置。关于配置的优先级，axios文档说的很清楚，这个顺序是：在 lib/defaults.js 找到的库的默认值，然后是实例的 defaults 属性，最后是请求的 config 参数。后者将优先于前者。
// 3.restful风格的接口，也可以通过这种方式灵活的设置api接口地址。


