import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const  state={
  name:'test',
  userName:'尚未登录的人',

};
const mutations={
  userNameChange(state,newName){
      state.userName=newName
  }
}

export default new Vuex.Store({
  state,
  mutations
})



