/**
 * 接口域名的管理
 * 通过base.js来管理我们的接口域名，不管有多少个都可以通过这里进行接口的定义。即使修改起来，也是很方便的。
 */
const base = {
  localHost: 'http://localhost:8080/pkServer',
  localServer:'http://192.168.1.145:8080/pkServer', //本地服务器地址
  serverHost:'http://192.168.1.142:8080/pkServer', //服务器地址
}
export default base;




